.height {
  height: 30vh;
}

.divider {
  height: 20vh !important;
  color: white;
}

.form {
  position: relative;
}

.form .fa-search {

  position: absolute;
  top: 20px;
  left: 20px;
  /* color: #9ca3af; */

}

.form span {

  position: absolute;
  right: 17px;
  top: 13px;
  padding: 2px;
  border-left: 1px solid #d1d5db;

}

.left-pan {
  padding-left: 7px;
}

.left-pan i {

  padding-left: 10px;
}

.form-input {

  height: 55px;
  text-indent: 33px;
  border-radius: 10px;
}

.form-input:focus {

  box-shadow: none;
  border: none;
}

.playlist {
  display: none;
}

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
body {
    margin: 0;
    padding: 0;
    font-family: "Poppins", sans-serif;
    box-sizing: border-box;
}

:root {
    --primary-color: #fd2d47;
    --background-color: rgb(17, 22, 42);
    --background-accent-color: rgb(44, 56, 86);
    --background-secondary-color: rgb(60, 62, 77);
    --text-color: rgb(255, 255, 255);
    --secondary-text-color: rgb(122, 124, 133);
    --yellow: rgb(240, 239, 11);
    --yellow-accent: rgb(161, 161, 11);
    --red-pale: rgb(239, 106, 107);
    --button-follow: rgb(255, 255, 255);
    --hint-color: rgba(255, 255, 255, 0.15);
    --main-padding-sides: 40px;
    --main-padding-sides-mobile: 18px;
}

[data-theme="light"] {
    --primary-color: rgb(74, 178, 169);
    --background-color: rgb(255, 255, 255);
    --background-accent-color: rgb(243, 243, 245);
    --background-secondary-color: rgb(246, 246, 246);
    --text-color: rgb(13, 13, 13);
    --secondary-text-color: rgb(51, 51, 51);
    --yellow: rgb(74, 178, 169);
    --yellow-accent: rgb(54, 141, 134);
    --button-follow: rgba(0, 0, 0, 0.2);
    --hint-color: rgba(0, 0, 0, 0.15);
}

a {
    color: transparent;
}

#root {
    height: 100vh;
}